.ng-ui {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  bottom: 0px;
  pointer-events: none;
  z-index: 1000;
}

.ng-ui_pop-up {
  width: 400px;
  height: 400px;
  padding: 10px;
  box-sizing: border-box;
}

.ng-ui_pop-up_body {
  pointer-events: all;
  cursor: default;
  width: 100%;
  height: 100%;
  border: 1.5px solid #b9becb;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0 3px 17px -5px #000;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-sizing: border-box;
}

.ng-ui_pop-up_body_search-row {
  height: 40px;
  min-height: 40px;
  padding: 10px;
  box-sizing: content-box;
}

.ng-ui_pop-up_body_search-row__input {
  width: 100%;
  border-radius: 5px;
  height: 100%;
  border: 1px solid #b9b9b9;
  outline: 0px;
  padding: 10px;
  box-sizing: border-box;
  box-shadow: 0 0 10px -5px #000 inset;
}

.ng-ui_pop-up_body_radio-row {
  background: #f5f5f5;
  height: 30px;
  min-height: 30px;
  display: flex;
  justify-content: space-around;
  border-top: 1px solid #dce0e8;
  border-bottom: 1px solid #dce0e8;
}

.ng-ui_pop-up_body_radio-row__radio-button {
  white-space: nowrap;
  display: flex;
  align-items: center;
  overflow: hidden;
  cursor: pointer;

  input.ng-ui_pop-up_body_radio-row__radio-button-input {
    margin: 0px 5px 0px 0px;
    cursor: pointer;
  }

  label {
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    font-family: sans-serif;
    font-weight: 700;
    color: #687077;
    margin: 0px;
  }
}

.ng-ui_pop-up_body_links-row {
  flex-grow: 1;
  min-height: 1px;
  padding: 10px;
  padding-top: 0px;
  display: flex;
  opacity: 0.9;
}

.ng-ui_pop-up_body_links-row_container {
  background-color: white;
  flex-grow: 1;
  border: 1px solid #b9b9b9;
  border-radius: 5px;
  box-shadow: 0px 0px 10px -4px black inset;
  overflow: hidden; 
}

.ng-ui_pop-up_body_links-row_container_connection-list {
  margin: 0px;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.ng-ui_pop-up_body_links-row_container_connection-list_row {
  margin-bottom: 5px;
  background: #f3f3f3;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #e0dfdf;
  display: flex;
  align-items: center;
  user-select: none;
  box-sizing: border-box;
  cursor: pointer;
  
  input[type=checkbox] {
    margin: 0px;
  }

  &:hover {
    background: #e3e3e3;
  }

  &:active {
    box-shadow: inset 0px 0px 7px -4px black;
  }
}

.ng-ui_pop-up_body_links-row_container_connection-list_row__label {
  color: #687077;
  font-family: sans-serif;
  font-weight: bold;
  font-size: 14px;
  flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-left: 5px;
}

.ng-ui_pop-up_body_links-row_container_connection-list_row__incoming-image,
.ng-ui_pop-up_body_links-row_container_connection-list_row__outgoing-image {
    height: 20px;
    margin-left: 5px;
    margin-right: 10px;
    opacity: 0.4;
    width: 30px;
}

.ng-ui_pop-up_body_links-row_container_connection-list_row__incoming-image, .ng-incoming-connections {
  content: url('../images/svg/incoming.svg');
}

.ng-ui_pop-up_body_links-row_container_connection-list_row__outgoing-image, .ng-outgoing-connections {
  content: url('../images/svg/outgoing.svg');
}

.ng-all-connections {
  height: 13px;
  margin-right: 5px;
  content: url('../images/svg/bothDirections.svg');
  opacity: 0.6;
}

.ng-incoming-connections {
  margin-right: 5px;  
  height: 13px;
  opacity: 0.6;
}

.ng-outgoing-connections {
  margin-right: 5px;  
  height: 13px;
  opacity: 0.6;
}

.ng-ui_pop-up_body_transitive-row {
    padding: 4px;
    display: flex;
    box-sizing: border-box;
    align-items: center;
    padding-left: 10px;
    opacity: 0.8;
    cursor: pointer;
    min-height: 25px;
    
    input[type=checkbox] {
      pointer-events: none;
      cursor: pointer;
      margin: 0px;
    }

    label {
      overflow: hidden;
      text-overflow: ellipsis;
      margin: 0px;
      margin-left: 5px;
      font-family: sans-serif;
      font-weight: bold;
      font-size: 13px;
      color: #687077;
      cursor: pointer;
    }

    &:hover {
      opacity: 0.9;
    }

    &:active {
      opacity: 1;
    }
}

.ng-ui_pop-up_body_links-row_container_connection-list_row__bubble {
  background-color: #cee1f3;
  min-width: 20px;
  height: 20px;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #6d6c90;
  font-weight: bold;
  font-family: sans-serif;
  border: 1px solid #abc6ff;
  overflow: hidden;
  text-overflow: ellipsis;
}

.ng-ui_pop-up_body_links-row__disabled {
  background-color: white;
  flex-grow: 1;
  border: 1px solid #b9b9b9;
  border-radius: 5px;
  box-shadow: 0px 0px 10px -5px black inset;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  text-align: center;
  font-size: x-large;
  font-family: sans-serif;
  font-weight: bold;
  color: #adbcca;
  background: #f5f7f9;
}

.ng-ui_pop-up_body_buttons-row {
  border-top: 1px solid #dce0e8;
  background: #f5f5f5;
  height: 45px;
  min-height: 45px;
  display: flex;
  padding: 10px;
  box-sizing: border-box;
  justify-content: space-between;
}

