.ng-ui_pop-up_body_buttons-row_button.go-button{
  border-radius: 3px;
  background: white;
  box-shadow: inset 0px 0px 7px -3px black;
  outline: none;
  border: 1px solid #6886b9;
  font-family: sans-serif;
  font-weight: bold;
  color: #68708a;
  margin: 0px;
  background-color: #54a954;
  color: white;
  
  &:hover {
    box-shadow: inset 0px 0px 7px -1px black;
  }

  &:active {
    box-shadow: inset 0px 0px 6px 0px black;
  }
}
