.default-color-class{
    fill: #ab9ac0;
    stroke: #8d819e;
}
text.default-color-class{ stroke: none; }

.bad-node-position{
    cursor: not-allowed;
    stroke: red!important;
    fill: #ff7b7b!important;
    opacity: 0.8;
}
text.bad-node-position{ stroke: none; }

$colors:
    (#f3ab91 #af5353)
    (#e3c128 #a26d0e)
    (#b4f787 #188a10)
    (#7beabf #1c7a60)
    (#97e2f2 #166fba)
    (#9ebffa #214b98)
    (#a9a4d9 #3b307a)
    (#c58fdc #6d2585)
    (#f58792 #931919)
    (#cecece #777);

@for $i from 1 through length($colors) {
  $clr: nth($colors, $i);
  .color-class-#{$i} {
    fill: nth($clr,1);
    stroke: nth($clr,2);
  }
  text.color-class-#{$i}{ stroke: none; }
}

.focus-node{
    fill: white;
    stroke: green;
}

.color-class-sub-class-of{
    fill: #84e484;
    stroke: green;
}
text.color-class-sub-class-of{ stroke: none; }

.color-class-sub-class-of{
    fill: #84e484;
    stroke: green;
}
text.color-class-sub-class-of{ stroke: none; }
