/* *********************** begin colors *********************** */
@import "color-classes";
/* *********************** end colors *********************** */

/* *********************** begin drop-down *********************** */
@import "pop-up-menu";
/* *********************** end drop-down *********************** */

/* *********************** begin main *********************** */

.tq-button {
    box-shadow: inset 0 0 14px 0px white;
    padding: 0px 10px;
    background-color: whitesmoke;
    border-radius: 5px;
    border: solid #b2b2b2;
    border-width: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    user-select: none;
    overflow: hidden;
    outline: 0px;

    &:disabled {
        background-color: whitesmoke;
        box-shadow: 0px 0 0 0 white!important;
        cursor: not-allowed;
    }

    &:hover {
        background-color: white;
    }

    &:active {
        background-color: whites;
        box-shadow: inset 0 0 14px -3px #c5c5c5;
    }
}

.tq-ng-root {
  width: 100%;
  height: 100%;
  border: 1px solid whitesmoke;
  box-sizing: border-box;
  overflow: hidden;
  position: relative;

  svg {
    background: white;
  }
}

.tool-remove,
.tool-options,
.port-label,
.marker-arrowhead-group {
  display: none!important;
}

.paper-scroller {
  position: absolute;
}

.halo-element {
  opacity: 0.3;
  position: absolute;

  &.disabled {
    cursor: not-allowed;
    opacity: 0.1;
  }
}
.halo-element:hover {
  opacity: 1;

  &.disabled {
    opacity: 0.1;
  }
}

.expand-collapse {
  @extend .halo-element;
  bottom: -25px;
  right: 8%;
}

.expand-collapse-reverse {
  @extend .halo-element;
  bottom: -25px;
  left: 8%;
}

.set-focus-node {
  @extend .halo-element;
  bottom: -25px;
  left: 8%;
}

.set-focus-node-reverse {
  @extend .halo-element;
  bottom: -25px;
  right: 8%;
}

.reset-node-position {
  @extend .halo-element;
}

.expansion-config, .expansion-config-reverse, .expansion-config-modified, .expansion-config-modified-reverse {
  @extend .halo-element;
  bottom: -25px;
}

.expansion-config-reverse, .expansion-config-modified-reverse {
    left: calc(8% + 23px);
}

.expansion-config, .expansion-config-modified {
    bottom: -25px;
    right: calc(8% + 23px);
}

.halo-of-root-node {
  border-radius: 0px!important;
}

.halo.joint-theme-default .handle {
  background-size: 15px 15px;
}

.dialog.joint-theme-default.modal {
    display: inherit;
}


// ================================================================
//  Pagination
// ================================================================

.ng-pagination-nodes {
  border: 1px solid #333;
  border-radius: 15px;
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  transition: opacity 0.1s;

  .ng-prev, .ng-next {
    border: 0 none;
    box-sizing: border-box;
    cursor: pointer;
    height: 16px;
    padding: 0;
    position: absolute;
    top: 50%;
    margin-top: -8px;
    width: 16px;
    transition: 0.3s;

    &:focus {
      outline: none;
    }

    &:disabled {
      opacity: 0.4!important;
      cursor: not-allowed;
    }
  }

  .ng-prev {
    background: url('../images/svg/prevPage.svg');
    left: 2px;
  }

  .ng-next {
    background: url('../images/svg/nextPage.svg');
    right: 2px;
  }

  .ng-pages {
    color: #333;
    font-family: 'Helvetica', 'Arial', sans-serif;
    font-size: 12px;
    text-align: center;
    padding: 3px 0 4px;
    position: absolute;
    top: 0;
    left: 17px;
    right: 17px;
    white-space: nowrap;
  }

  &.loading {
    opacity: 0.3;
  }
}

.ng-pagination-edges {
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  transition: opacity 0.1s;

  .ng-prev, .ng-next {
    background: #fff;
    border: 0 none;
    border-left: 1px solid #333;
    border-right: 1px solid #333;
    box-sizing: border-box;
    cursor: pointer;
    height: 15px;
    padding: 0;
    position: absolute;
    left: 50%;
    margin-left: -50%;
    width: 100%;
    transition: 0.3s;

    opacity: 0;
    visibility: hidden;

    &:focus {
      outline: none;
    }

    &:before {
      background-position: center;
      background-size: cover;
      content: "";
      height: 10px;
      position: absolute;
      top: 50%;
      left: 50%;
      margin: -5px 0 0 -5px;
      width: 10px;
    }

    &:disabled {
      opacity: 0.5!important;
      cursor: not-allowed;
    }
  }

  .ng-next {
    border-top: 1px solid #333;
    border-radius: 7px 7px 0 0;
    background: #e4e4e4;

    &:before {
      background-image: url('../images/svg/nextPageVertical.svg');
    }
  }

  .ng-prev {
    border-bottom: 1px solid #333;
    border-radius: 0 0 7px 7px;
    background: #e4e4e4;
    bottom: 0;

    &:before {
      background-image: url('../images/svg/prevPageVertical.svg');
    }
  }

  .ng-next:hover, .ng-prev:hover {
    background: #b9b9b9;
  }
  .ng-next:active, .ng-prev:active {
    background: #a0a0a0;
  }

  .ng-pages {
    background: #fff;
    border: 1px solid #333;
    box-sizing: border-box;
    color: #333;
    font-family: 'Helvetica', 'Arial', sans-serif;
    font-size: 10px;
    height: 16px;
    line-height: 14px;
    position: absolute;
    top: 15px;
    bottom: 15px;
    text-align: center;
    width: 100%;
    white-space: nowrap;
  }

  &.active {
    .ng-prev, .ng-next {
      opacity: 1;
      visibility: visible;
    }
  }

  &.loading {
    opacity: 0.3;
  }
}

.reset-node-position {
  background-image: url('../images/svg/reset.svg');
}

.expansion-config,
.expansion-config-reverse {
  background-image: url('../images/svg/properties.svg');
}

.expansion-config-modified,
.expansion-config-modified-reverse {
  background-image: url("../images/svg/propertiesModified.svg");
}

.set-focus-node, .set-focus-node-reverse {
  background-image: url('../images/svg/setFocusNode.svg');
}

.expand-collapse.ng-expand, .expand-collapse-reverse.ng-expand {
  background-image: url('../images/svg/expand.svg');
}

.expand-collapse.ng-collapse, .expand-collapse-reverse.ng-collapse {
  background-image: url('../images/svg/collapse.svg');
}
/* *********************** end main *********************** */
